[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  font-size: initial;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}
.icon-arrows-cw:before {
  content: '\E800';
}
/* '' */
.icon-minus:before {
  content: '\E801';
}
/* '' */
.icon-plus:before {
  content: '\E802';
}
/* '' */
.icon-help-circled:before {
  content: '\E803';
}
/* '' */
.icon-cancel:before {
  content: '\E804';
}
/* '' */
*, *:before, *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
html {
  font-size: 5vh;
}
html,
body {
  -webkit-font-smoothing: antialiased;
}
/* Space out content a bit */
body {
  background-color: #2b2828;
}
/* Everything but the jumbotron gets side spacing for mobile first views */
.header,
.marketing,
.footer {
  padding-left: 15px;
  padding-right: 15px;
}
/* Custom page header */
.header {
  /* Make the masthead heading the same height as the navigation */
}
h1,
h2 {
  color: #fff;
  margin-top: 0;
  font-family: 'Asap';
}
h1 {
  color: #96d56c;
  font-family: 'Asap';
  font-weight: bold;
  text-align: center;
  width: 80%;
  font-size: 30px;
  margin-bottom: 0;
  letter-spacing: 1px;
}
h1 .letter-2 {
    color: #e78124;
}
h1 .letter-3 {
    color: #6babc6;
}
h1 .letter-4 {
    color: #f7f230;
}
h2 {
  font-size: 16px;
  font-weight: normal;
  color: #d2babb;
  text-align: left;
  padding-left: .5rem;
  margin-bottom: .5rem;
  line-height: .5rem;
}
p {
  color: #fff;
  font-family: 'Asap';
  font-size: 16px;
  padding: 0;
  margin-bottom: 10px;
}
button {
  border: none;
  border-radius: 0;
  font-family: 'Asap';
  font-weight: 700;
  text-align: center;
  -webkit-tap-highlight-color: transparent;
}
button:focus {
    outline: none;
}
header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-bottom: 1.5rem;
  position: relative;
  height: 2rem;
}
.help {
  background-color: #191717;
  color: #e63d4a;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  width: 20%;
  font-size: 30px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
.help.open {
    color: #b21f30;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
}
.help:before {
    font-size: 24px;
}
.how-to-play {
  background-color: #433c3d;
  padding: .5rem 1rem;
  border-top: 1px solid #191717;
  border-bottom: 1px solid #191717;
  margin-bottom: 0.5rem;
  position: absolute;
  top: 100%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-box-shadow: 0px 10px 15px 5px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 10px 15px 5px rgba(0, 0, 0, 0.2);
}
.how-to-play h2 {
    padding-left: 0;
}
.how-to-play.show {
    top: 100%;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.container {
  position: relative;
  height: 100%;
}
.display {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  margin-bottom: 1.5rem;
  border-top: 1px solid #191717;
  border-bottom: 1px solid #191717;
}
.display-text {
  background-color: #433c3d;
  border: none;
  color: #fff;
  display: block;
  float: left;
  font-family: 'Asap';
  font-size: 22px;
  font-weight: bold;
  height: 2rem;
  text-align: center;
  width: 80%;
  -webkit-box-shadow: inset 0px 0 0px 0px #ffffff;
          box-shadow: inset 0px 0 0px 0px #ffffff;
  -webkit-transition: -webkit-box-shadow 0.25s;
  transition: -webkit-box-shadow 0.25s;
  transition: box-shadow 0.25s;
  transition: box-shadow 0.25s, -webkit-box-shadow 0.25s;
}
.display-text:focus {
    outline: 0;
    -webkit-box-shadow: inset 0px 0 0px 5px #ffffff;
            box-shadow: inset 0px 0 0px 5px #ffffff;
}
.display-refresh {
  background-color: #4797b7;
  -webkit-box-shadow: inset 5px 0 10px rgba(0, 0, 0, 0.1);
          box-shadow: inset 5px 0 10px rgba(0, 0, 0, 0.1);
  color: #fff;
  display: block;
  font-size: 24px;
  height: 2rem;
  -webkit-transition: background-color 0.35s;
  transition: background-color 0.35s;
  width: 20%;
}
.display-refresh:active {
    background-color: #4bb4d4;
    -webkit-transition: background-color 0s;
    transition: background-color 0s;
}
.display-refresh:before {
    font-size: 20px;
}
.display-button-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  height: 2rem;
  width: 100%;
}
.display-button-bar button {
    font-size: 20px;
    height: 100%;
    width: 20%;
    -webkit-box-shadow: inset 5px 0 10px rgba(0, 0, 0, 0.1);
            box-shadow: inset 5px 0 10px rgba(0, 0, 0, 0.1);
    -webkit-transition: background-color 0.35s;
    transition: background-color 0.35s;
}
.display-button-bar button:first-child {
      background-color: #f6f230;
}
.display-button-bar button:first-child:active {
        background-color: #faf892;
}
.display-button-bar button:nth-child(2) {
      background-color: #f2d02c;
}
.display-button-bar button:nth-child(2):active {
        background-color: #f8e58c;
}
.display-button-bar button:nth-child(3) {
      background-color: #eda829;
}
.display-button-bar button:nth-child(3):active {
        background-color: #f5ce87;
}
.display-button-bar button:nth-child(4) {
      background-color: #e78124;
}
.display-button-bar button:nth-child(4):active {
        background-color: #f1b680;
}
.display-button-bar button:last-child {
      background-color: #e56621;
}
.display-button-bar button:last-child:active {
        background-color: #f0a57c;
}
.display-button-bar button[disabled] {
      background-color: #ccc;
}
.display-button-bar button[disabled]:active {
        background-color: #fd686d;
}
.display-button-bar button:active {
      -webkit-transition: background-color 0s;
      transition: background-color 0s;
}
.letters {
  margin-bottom: 80px;
}
.letters .display-text {
    letter-spacing: 2px;
    text-transform: uppercase;
}
.new-round {
  background-color: #7bc944;
  color: #222;
  font-size: 20px;
  height: 4rem;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}
.new-round:active {
    background-color: #a1f43c;
    -webkit-transition: background-color 0s;
    transition: background-color 0s;
}
/* Responsive: Portrait tablets and up */
@media screen and (min-width: 640px) {
.container {
    max-width: 640px;
    margin: 0 auto;
}
  /* Remove the padding we set earlier */
.header,
  .marketing,
  .footer {
    padding-left: 0;
    padding-right: 0;
}
  /* Space out the masthead */
.header {
    margin-bottom: 30px;
}
  /* Remove the bottom border on the jumbotron for visual effect */
.jumbotron {
    border-bottom: 0;
}
}
[class^="icon-"], [class*=" icon-"] {
  font-size: 0 !important;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
